.paginationWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
}

.pagination {
  display: flex;
  justify-content: center;
  list-style: none;
  cursor: pointer;
  gap: 7px;
  padding: 12px 8px;
  background: #fef9f9;
  box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
  border-radius: 45px;
}

.pagination a {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  color: #cce4fb;
  width: 35px;
  height: 35px;
  border-radius: 100%;
  border: 1px solid #cce4fb;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.pagination a:hover,
.pagination a:focus {
  background-color: #54adff;
  border-color: #54adff;
  color: #fef9f9;
}

.paginationActive a {
  background-color: #54adff;
  border-color: #54adff;
  color: #fef9f9;
}

.title {
  font-size: 24px;
  line-height: 1.4;
  font-weight: 700;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 24px;

  @media screen and (min-width: 768px) {
    margin-top: 80px;
    font-size: 48px;
    margin-bottom: 40px;
  }
}
